import AppState from '../AppState/AppState';

const toolBox = document.getElementById('tool-size');
function changeToolSize(e) {
  if (e.target.classList.contains('tool-size-picker')) {
    document.querySelector('div.tool-size-picker.active').classList.remove('active');
  }
  switch (e.target.getAttribute('data-size')) {
    case '1':
      e.target.classList.add('active');
      AppState.toolSize = 1;
      break;
    case '2':
      e.target.classList.add('active');
      AppState.toolSize = 2;
      break;
    case '3':
      e.target.classList.add('active');
      AppState.toolSize = 3;
      break;
    case '4':
      e.target.classList.add('active');
      AppState.toolSize = 4;
      break;
    default:
      break;
  }
}

toolBox.addEventListener('click', changeToolSize);
