import AppState from '../AppState/AppState';
import tools from './tools';

function toolPick(toolButton) {
  if (toolButton.classList.contains('tool')) {
    document.querySelector('div.tool.active').classList.remove('active');
    toolButton.classList.add('active');
  }
  const i = +toolButton.getAttribute('data-index');
  AppState.currentTool = tools[i];
}

function toolPickClick(e) {
  let toolButton = e.target;

  if (e.target.classList.contains('tool')) {
    toolButton = e.target;
  }
  if (e.target.parentNode.classList.contains('tool')) {
    toolButton = e.target.parentNode;
  }
  toolPick(toolButton);
}

document.getElementById('tools-box').addEventListener('click', toolPickClick);
