
import AppState from '../AppState/AppState';

let frameRate = 1;
const fpsShow = document.getElementById('fps-show');

function changeFrameRate() {
  frameRate = this.value;
  fpsShow.innerHTML = frameRate;
  AppState.frameRate = frameRate;
}

document.getElementById('fps').addEventListener('input', changeFrameRate);
let count = 0;
const frameWithAnimation = document.getElementById('animation');
let start;
let current = 0;
let requestId;
const viewCollection = document.getElementsByClassName('canvas');
function step(timestamp) {
  if (!start) start = timestamp;
  const progress = Math.round((timestamp - start) / 1000 * frameRate);
  if (current !== progress) {
    current = progress;
    const dataUrl = viewCollection[count].toDataURL();
    frameWithAnimation.style.background = `url(${dataUrl}) no-repeat`;
    frameWithAnimation.style.backgroundSize = 'contain';
    count += 1;
    if (count >= viewCollection.length) {
      count = 0;
    }
  }
  requestId = requestAnimationFrame(step);
}
function startAnimation() {
  requestId = requestAnimationFrame(step);
}
function stopAnimation() {
  cancelAnimationFrame(requestId);
}
function fullScreen() {
  frameWithAnimation.webkitRequestFullscreen();
}
const startButton = document.getElementById('start');
const stopButton = document.getElementById('stop');
const fullScreenButton = document.getElementById('fullscreen');
startButton.addEventListener('click', startAnimation);
stopButton.addEventListener('click', stopAnimation);
fullScreenButton.addEventListener('click', fullScreen);
