/* eslint-disable consistent-return */
import AppState from '../AppState/AppState';

/* Rewraite numeration of frames when change --------------------------------------------- */
const framesCollection = document.getElementsByClassName('frame-container');
function recalculateNumeration() {
  for (let i = 0; i < framesCollection.length; i += 1) {
    framesCollection[i].childNodes[0].childNodes[0].innerHTML = (i + 1);
  }
}

/* Clear current canvas ------------------------------------------------------------------ */
const viewCollection = document.getElementsByClassName('canvas');
const frameState = {
  currentActiveFrame: 0,
  canvas: '',
  context: '',
};

function clearCanvas() {
  frameState.canvas.width = frameState.canvas.width;
  frameState.context = frameState.canvas.getContext('2d');

  const dataUrl = frameState.canvas.toDataURL();
  framesCollection[frameState.currentActiveFrame].style.background = `url(${dataUrl}) no-repeat`;
  framesCollection[frameState.currentActiveFrame].style.backgroundSize = 'contain';
}

/* Activation frame on click -------------------------------------------------------------- */
function activateFrame(number) {
  framesCollection[frameState.currentActiveFrame].classList.remove('active-frame');
  viewCollection[frameState.currentActiveFrame].classList.add('not-active-canvas');
  frameState.currentActiveFrame = number;
  framesCollection[number].classList.add('active-frame');
  viewCollection[number].classList.remove('not-active-canvas');
  frameState.canvas = viewCollection[number];
  frameState.context = frameState.canvas.getContext('2d');
}

/* Adding new frame --------------------------------------------------------------------- */
const framesLayout = document.getElementById('frames-layout');
const view = document.getElementById('view');
const viewWidth = parseInt(getComputedStyle(view).width, 10);
const viewHeight = parseInt(getComputedStyle(view).height, 10);
const canvasSideSize = Math.floor(Math.min.apply(null, [viewWidth, viewHeight]) / 128) * 128;
AppState.canvasSideSize = canvasSideSize;
AppState.viewWidth = viewWidth;
AppState.viewHeight = viewHeight;
function addNewFrame(nextSiblingFrame, nextSiblingCanvas, number) {
  const div = document.createElement('div');
  div.className = 'frame-container';
  div.innerHTML = `<div class = "frame-number"><div>${framesCollection.length + 1}</div></div>
  <div class = "frame-duplicate"><div><span  class="material-icons">file_copy</span></div></div>
  <div class = "frame-delete"><div><span  class="material-icons">delete_forever</span></div></div>
  <div class = "frame-move"><div><span  class="material-icons">swap_vert</span></div></div>`;
  if (arguments.length === 1) {
    framesLayout.insertBefore(div, undefined);
  } else {
    framesLayout.insertBefore(div, nextSiblingFrame);
  }

  const canv = document.createElement('canvas');
  canv.className = 'canvas';
  canv.width = canvasSideSize;
  canv.height = canvasSideSize;
  view.insertBefore(canv, nextSiblingCanvas);
  if (typeof number !== 'number') {
    activateFrame(framesCollection.length - 1);
  } else {
    activateFrame(number + 1);
    recalculateNumeration();
  }
}

/* -------------------------------------------------------------------------------------- */
function getTargetIndex(target) {
  for (let i = 0; i < framesCollection.length; i += 1) {
    if (target === framesCollection[i]) return i;
  }
}

/* -------------------------------------------------------------------------------------- */
function duplicateFrame(number) {
  let nextSiblingFrame;
  let nextSiblingCanvas;

  if (frameState.currentActiveFrame > number) {
    frameState.currentActiveFrame += 1;
  }
  if (number !== framesCollection.length - 1) {
    nextSiblingFrame = framesCollection[number + 1];
    nextSiblingCanvas = viewCollection[number + 1];
  }

  const canvas1 = viewCollection[number];
  const context1 = canvas1.getContext('2d');
  const imgd = context1.getImageData(0, 0, canvasSideSize, canvasSideSize);
  addNewFrame(nextSiblingFrame, nextSiblingCanvas, number);
  const canvas2 = viewCollection[frameState.currentActiveFrame];
  const context2 = canvas2.getContext('2d');
  context2.putImageData(imgd, 0, 0);
  const dataUrl = canvas2.toDataURL();
  framesCollection[frameState.currentActiveFrame].style.background = `url(${dataUrl}) no-repeat`;
  framesCollection[frameState.currentActiveFrame].style.backgroundSize = 'contain';
}

/* -------------------------------------------------------------------------------------- */
function deleteFrame(number) {
  if (number === frameState.currentActiveFrame) {
    if (framesCollection.length === 1) {
      clearCanvas();
    }
    if (number === 0 && framesCollection.length > 1) {
      activateFrame(number + 1);
      framesCollection[number].remove();
      viewCollection[number].remove();
      frameState.currentActiveFrame -= 1;
    } else if (number !== 0 && framesCollection.length > 1) {
      activateFrame(number - 1);
      framesCollection[number].remove();
      viewCollection[number].remove();
    }
  } else if (number < frameState.currentActiveFrame) {
    framesCollection[number].remove();
    viewCollection[number].remove();
    frameState.currentActiveFrame -= 1;
  } else if (number > frameState.currentActiveFrame) {
    framesCollection[number].remove();
    viewCollection[number].remove();
  }
  recalculateNumeration();
}
/* ---------------------------------------------------------------------- */
function moveFrame(event, number) {
  const dragElement = framesCollection[number];
  dragElement.style.zIndex = 10;
  let shiftX;
  let startX;
  let shiftY;
  let newX;
  let newY;

  function moveAt(clientX, clientY) {
    newX = clientX - shiftX;
    newY = clientY - shiftY;
    dragElement.style.left = `${newX}px`;
    dragElement.style.top = `${newY}px`;
  }
  function startDrag(clientX, clientY) {
    shiftX = clientX - dragElement.getBoundingClientRect().left;
    startX = clientX;
    shiftY = clientY - dragElement.getBoundingClientRect().top + 10;
    dragElement.style.position = 'absolute';

    moveAt(clientX, clientY);
  }
  startDrag(event.clientX, event.clientY);
  function finishDrag() {
    const downElement = document.elementFromPoint(newX, newY);
    if (downElement.classList.contains('frame-container')) {
      dragElement.style.zIndex = 2;
      const downElementNumber = getTargetIndex(downElement);
      const dragElementNumber = getTargetIndex(dragElement);
      framesCollection[frameState.currentActiveFrame].classList.remove('active-frame');
      viewCollection[frameState.currentActiveFrame].classList.add('not-active-canvas');
      dragElement.parentNode.insertBefore(dragElement, downElement);
      const dragCanvas = viewCollection[dragElementNumber];
      const downCanvas = viewCollection[downElementNumber];
      dragCanvas.parentNode.insertBefore(dragCanvas, downCanvas);
      activateFrame(downElementNumber);
      recalculateNumeration();
    }
    dragElement.style.position = '';
    document.onmousemove = null;
    dragElement.onmouseup = null;
  }
  document.onmousemove = (e) => {
    moveAt(startX, e.clientY);
  };

  dragElement.onmouseup = () => {
    finishDrag();
  };
  return false;
}
/* ---------------------------------------------------------------------- */
function frameButtonsDo(event) {
  if (event.target.className === 'frame-container') {
    const number = getTargetIndex(event.target);
    activateFrame(number);
  }
  if (event.target.className === 'frame-duplicate') {
    const number = getTargetIndex(event.target.parentNode);
    duplicateFrame(number);
  } else if (event.target.parentNode.parentNode.className === 'frame-duplicate') {
    const number = getTargetIndex(event.target.parentNode.parentNode.parentNode);
    duplicateFrame(number);
  }
  if (event.target.className === 'frame-delete') {
    const number = getTargetIndex(event.target.parentNode);
    deleteFrame(number);
  } else if (event.target.parentNode.parentNode.className === 'frame-delete') {
    const number = getTargetIndex(event.target.parentNode.parentNode.parentNode);
    deleteFrame(number);
  }
}
function isMoveFrame(event) {
  if (event.target.className === 'frame-move') {
    const number = getTargetIndex(event.target.parentNode);
    moveFrame(event, number);
  } else if (event.target.parentNode.parentNode.className === 'frame-move') {
    const number = getTargetIndex(event.target.parentNode.parentNode.parentNode);
    moveFrame(event, number);
  }
}
framesLayout.addEventListener('click', frameButtonsDo);
framesLayout.addEventListener('mousedown', isMoveFrame);

const addButton = document.getElementById('add-new-frame');
addButton.addEventListener('click', addNewFrame);
const clearButton = document.getElementById('clear-canvas');
clearButton.addEventListener('click', clearCanvas);

addNewFrame();
frameState.canvas = viewCollection[frameState.currentActiveFrame];
frameState.context = frameState.canvas.getContext('2d');
framesCollection[0].classList.add('active-frame');

export {
  view, frameState, framesCollection, viewCollection,
};
