import AppState from '../AppState/AppState';
import GIF from '../../lib/gif/gif';
// import APNGencoder from '../../lib/apng/apng';
const APNGencoder = require('../../lib/apng/apng');

const canvasCollection = document.getElementsByClassName('canvas');
function exportToGif() {
  const gif = new GIF({
    workers: 2,
    quality: 10,
  });
  for (let i = 0; i < canvasCollection.length; i += 1) {
    const bufferCanvas = document.createElement('canvas');
    bufferCanvas.width = AppState.canvasSize;
    bufferCanvas.height = AppState.canvasSize;
    bufferCanvas.getContext('2d').drawImage(canvasCollection[i], 0, 0, AppState.canvasSize, AppState.canvasSize);
    gif.addFrame(bufferCanvas, { delay: 100 / AppState.frameRate });
    bufferCanvas.remove();
  }
  gif.render();
  gif.on('finished', (blob) => {
    const a = document.createElement('a');
    a.href = URL.createObjectURL(blob);
    a.download = 'project.gif';
    a.click();
  });
}

function exportToApng() {
  const canvas = document.createElement('canvas');
  canvas.width = AppState.canvasSize;
  canvas.height = AppState.canvasSize;
  const ctx = canvas.getContext('2d');
  const encoder = new APNGencoder(canvas);
  encoder.setRepeat(0);
  encoder.setDelay(100 / AppState.frameRate);
  encoder.setDispose(1);
  encoder.setBlend(1);
  encoder.start();
  for (let i = 0; i < canvasCollection.length; i += 1) {
    ctx.clearRect(0, 0, canvas.width, canvas.height);
    ctx.drawImage(canvasCollection[i], 0, 0, AppState.canvasSize, AppState.canvasSize);
    encoder.addFrame(ctx);
  }
  encoder.finish();
  const out = encoder.stream();
  const a = document.createElement('a');
  a.href = URL.createObjectURL(new Blob([new Uint8Array(out.bin)], { type: 'image/png' }));
  a.download = 'animation.png';
  a.click();
}

const buttonGif = document.getElementById('gif-export');
buttonGif.addEventListener('click', exportToGif);
const buttonApng = document.getElementById('apng-export');
buttonApng.addEventListener('click', exportToApng);
