import {
  view, framesCollection, frameState,
} from '../frames/frames';
import AppState from '../AppState/AppState';

const mouse = { x: 0, y: 0 };
const { canvasSideSize } = AppState;
const deltaWidth = Math.floor((AppState.viewWidth - canvasSideSize) / 2);
const deltaHeight = Math.floor((AppState.viewHeight - canvasSideSize) / 2);
let draw = false;
let pixelSize = (canvasSideSize / AppState.canvasSize);
let toolSize;
let startX;
let startY;
let imgData;
function startDrawing(e) {
  imgData = frameState.context.getImageData(0, 0, canvasSideSize, canvasSideSize);
  pixelSize = (canvasSideSize / AppState.canvasSize);
  toolSize = pixelSize * AppState.toolSize;
  mouse.x = (Math.floor((e.pageX - this.offsetLeft - deltaWidth) / pixelSize)) * pixelSize;
  mouse.y = (Math.floor((e.pageY - this.offsetTop - deltaHeight) / pixelSize)) * pixelSize;
  startX = mouse.x;
  startY = mouse.y;
  draw = true;
  if (e.which === 1) {
    frameState.context.fillStyle = AppState.firstColor;
    frameState.context.strokeStyle = AppState.firstColor;
  }
  if (e.which === 3) {
    frameState.context.fillStyle = AppState.secondColor;
    frameState.context.strokeStyle = AppState.secondColor;
  }
  frameState.context.globalCompositeOperation = 'source-over';
  AppState.currentTool.mouseDown(e, frameState, mouse, toolSize, startX, startY, imgData);
}
const coordinates = document.getElementById('coordinates');
function whileDrawing(e) {
  mouse.x = (Math.floor((e.pageX - this.offsetLeft - deltaWidth) / pixelSize)) * pixelSize;
  mouse.y = (Math.floor((e.pageY - this.offsetTop - deltaHeight) / pixelSize)) * pixelSize;
  coordinates.innerHTML = `x: ${mouse.x}; y: ${mouse.y}`;
  if (draw === true) {
    AppState.currentTool.mouseMove(e, frameState, mouse, toolSize, startX, startY, imgData);
  }
}
function endDrawing() {
  draw = false;
  const dataUrl = frameState.canvas.toDataURL();
  framesCollection[frameState.currentActiveFrame].style.background = `url(${dataUrl}) no-repeat`;
  framesCollection[frameState.currentActiveFrame].style.backgroundSize = 'contain';
}
function blockContext(e) {
  e.preventDefault();
  e.stopPropagation();
  return false;
}
view.addEventListener('mousedown', startDrawing);
view.addEventListener('mousemove', whileDrawing);
view.addEventListener('mouseup', endDrawing);
view.addEventListener('contextmenu', blockContext);
