import AppState from '../AppState/AppState';

const pencil = {
  name: 'Pencil',
  icon: 'edit',
  hotkey: 'p',
  mouseDown: (e, frameState, mouse, toolSize) => {
    frameState.context.fillRect(mouse.x, mouse.y, toolSize, toolSize);
  },
  mouseMove: (e, frameState, mouse, toolSize) => {
    frameState.context.fillRect(mouse.x, mouse.y, toolSize, toolSize);
  },
  mouseUp: () => {
  },
};
const eraser = {
  name: 'Eraser',
  icon: 'broken_image',
  hotkey: 'e',
  mouseDown: (e, frameState, mouse, toolSize) => {
    const { context } = frameState;
    context.globalCompositeOperation = 'destination-out';
    frameState.context.fillRect(mouse.x, mouse.y, toolSize, toolSize);
  },
  mouseMove: (e, frameState, mouse, toolSize) => {
    frameState.context.fillRect(mouse.x, mouse.y, toolSize, toolSize);
  },
  mouseUp: () => {
  },
};
const paintBucket = {
  name: 'Paint bucket',
  icon: 'format_color_fill',
  hotkey: 'b',
  mouseDown: (e, frameState) => {
    frameState.context.fillRect(0, 0, AppState.canvasSideSize, AppState.canvasSideSize);
  },
  mouseMove: () => {
  },
  mouseUp: () => {
  },
};
const colorPicker = {
  name: 'Colot picker',
  icon: 'colorize',
  hotkey: 'i',
  mouseDown: (e, frameState, mouse) => {
    const imageData = frameState.context.getImageData(mouse.x, mouse.y, 1, 1).data;
    if (e.which === 1) {
      AppState.firstColor = `rgba(${imageData[0]},${imageData[1]},${imageData[2]},1)`;
      document.getElementById('color-label1').style.backgroundColor = AppState.firstColor;
    }
    if (e.which === 3) {
      AppState.secondColor = `rgba(${imageData[0]},${imageData[1]},${imageData[2]},1)`;
      document.getElementById('color-label2').style.backgroundColor = AppState.secondColor;
    }
  },
  mouseMove: () => {

  },
  mouseUp: () => {
  },
};
const rotate = {
  name: 'Rotate',
  icon: 'rotate_90_degrees_ccw',
  hotkey: 'p',
  mouseDown: (e, frameState, mouse, toolSize, startX, startY, imgData) => {
    frameState.context.save();
    frameState.context.translate(AppState.canvasSideSize / 2, AppState.canvasSideSize / 2);
    frameState.context.rotate(Math.PI / 2);
    frameState.context.putImageData(imgData, -AppState.canvasSideSize / 2,
      -AppState.canvasSideSize / 2);
    frameState.context.restore();
  },
  mouseMove: (e, frameState, mouse, toolSize) => {
    frameState.context.fillRect(mouse.x, mouse.y, toolSize, toolSize);
  },
  mouseUp: () => {
  },
};
const line = {
  name: 'Line',
  icon: 'remove',
  hotkey: 'l',
  mouseDown: () => {
  },
  mouseMove: (e, frameState, mouse, toolSize, startX, startY, imgData) => {
    const pixelSize = toolSize / AppState.toolSize;
    function calcStraightLine(startX1, startY1, endX1, endY1) {
      const coordinatesArray = [];
      let x1 = startX1;
      let y1 = startY1;
      const x2 = endX1;
      const y2 = endY1;
      const dx = Math.abs(x2 - x1);
      const dy = Math.abs(y2 - y1);
      const sx = (x1 < x2) ? (pixelSize) : -(pixelSize);
      const sy = (y1 < y2) ? (pixelSize) : -(pixelSize);
      let err = dx - dy;
      coordinatesArray.push([x1, y1]);
      while (!((x1 === x2) && (y1 === y2))) {
        const e2 = 2 * err;
        if (e2 > -dy) {
          err -= dy;
          x1 += sx;
        }
        if (e2 < dx) {
          err += dx;
          y1 += sy;
        }
        coordinatesArray.push([x1, y1]);
      }
      return coordinatesArray;
    }
    const coordinates = calcStraightLine(startX, startY, mouse.x, mouse.y);
    frameState.context.putImageData(imgData, 0, 0);
    for (let i = 0; i < coordinates.length; i += 1) {
      frameState.context.fillRect(coordinates[i][0], coordinates[i][1], toolSize, toolSize);
    }
    return (e, frameState, toolSize);
  },
  mouseUp: () => {
  },
};
const rectangle = {
  name: 'Rectangle',
  icon: 'crop_5_4',
  hotkey: 'r',
  mouseDown: () => {
  },
  mouseMove: (e, frameState, mouse, toolSize, startX, startY, imgData) => {
    const pixelSize = toolSize / AppState.toolSize;
    const { context } = frameState;
    context.putImageData(imgData, 0, 0);
    context.lineWidth = toolSize;
    const width = mouse.x - startX;
    const height = mouse.y - startY;
    const delta = pixelSize / 2;
    frameState.context.strokeRect(startX + delta, startY + delta, width, height);
    return (e, frameState);
  },
  mouseUp: () => {
  },
};
const circle = {
  name: 'Circle',
  icon: 'radio_button_unchecked',
  hotkey: 'l',
  mouseDown: () => {
  },
  mouseMove: (e, frameState, mouse, toolSize, startX, startY, imgData) => {
    const pixelSize = toolSize / AppState.toolSize;
    function calcStraightLine(startX1, startY1, endX1, endY1) {
      const coordinatesArray = [];
      const x1 = (Math.floor((startX1 + (endX1 - startX1) / 2) / pixelSize)) * pixelSize;
      const y1 = (Math.floor((startY1 + (endY1 - startY1) / 2) / pixelSize)) * pixelSize;
      let x = 0;
      let y = Math.sqrt((endX1 - x1) * (endX1 - x1) + (endY1 - y1) * (endY1 - y1));
      let delta = 2 - 2 * y;
      let error = 0;

      while (y >= 0) {
        coordinatesArray.push([x1 + x, y1 + y]);
        coordinatesArray.push([x1 + x, y1 - y]);
        coordinatesArray.push([x1 - x, y1 + y]);
        coordinatesArray.push([x1 - x, y1 - y]);
        error = 2 * (delta + y) - 1;
        if ((delta < 0) && (error <= 0)) {
          x += 1;
          delta += 2 * x + 1;
        } else if ((delta > 0) && (error > 0)) {
          y -= 1;
          delta -= 2 * y + 1;
        } else {
          x += 1;
          y += 1;
          delta += 2 * (x - y);
        }
      }
      return coordinatesArray;
    }
    const coordinates = calcStraightLine(startX, startY, mouse.x, mouse.y);
    frameState.context.putImageData(imgData, 0, 0);
    for (let i = 0; i < coordinates.length; i += 1) {
      frameState.context.fillRect(coordinates[i][0], coordinates[i][1], toolSize, toolSize);
    }
    return (e, frameState, toolSize);
  },
  mouseUp: () => {
  },
};

const tools = [pencil, eraser, paintBucket, colorPicker, line, rectangle, circle, rotate];

export default tools;
