import AppState from '../AppState/AppState';
import {
  framesCollection, viewCollection,
} from '../frames/frames';

let bufferCanvasSize;

function canvasesResize() {
  for (let i = 0; i < viewCollection.length; i += 1) {
    const canvas1 = viewCollection[i];
    const context1 = canvas1.getContext('2d');
    const imgd = context1.getImageData(0, 0, AppState.canvasSideSize, AppState.canvasSideSize);
    const canvas2 = document.createElement('canvas');
    canvas2.width = AppState.canvasSideSize;
    canvas2.height = AppState.canvasSideSize;
    canvas2.getContext('2d').putImageData(imgd, 0, 0);
    viewCollection[i].getContext('2d').clearRect(0, 0, AppState.canvasSideSize, AppState.canvasSideSize);
    const scaledSize = (bufferCanvasSize / AppState.canvasSize) * AppState.canvasSideSize;
    viewCollection[i].getContext('2d').imageSmoothingEnabled = false;
    viewCollection[i].getContext('2d').drawImage(canvas2, 0, 0, scaledSize, scaledSize);
    const dataUrl = viewCollection[i].toDataURL();
    framesCollection[i].style.background = `url(${dataUrl}) no-repeat`;
    framesCollection[i].style.backgroundSize = 'contain';
  }
}
function canvasSize(sizeButton) {
  if (sizeButton.classList.contains('canvas-size')) {
    document.querySelector('div.canvas-size.active').classList.remove('active');
    sizeButton.classList.add('active');
  }
  const size = +sizeButton.getAttribute('data-canvas-size');
  bufferCanvasSize = AppState.canvasSize;
  AppState.canvasSize = size;
  canvasesResize();
}

function canvasSizeClick(e) {
  let sizeButton = e.target;

  if (e.target.classList.contains('canvas-size')) {
    sizeButton = e.target;
  }
  if (e.target.parentNode.classList.contains('canvas-size')) {
    sizeButton = e.target.parentNode;
  }
  canvasSize(sizeButton);
}

document.getElementById('canvas-size-box').addEventListener('click', canvasSizeClick);
