import AppState from '../AppState/AppState';

const colorBlock1 = document.getElementById('color-block1');
const ctx1 = colorBlock1.getContext('2d');
const colorStrip1 = document.getElementById('color-strip1');
const ctx2 = colorStrip1.getContext('2d');
const colorBlock2 = document.getElementById('color-block2');
const ctx3 = colorBlock2.getContext('2d');
const colorStrip2 = document.getElementById('color-strip2');
const ctx4 = colorStrip2.getContext('2d');

const colorBlockWidth = colorBlock1.width;
const colorBlockHeight = colorBlock1.height;
const colorStripWidth = colorStrip1.width;
const colorStripHeight = colorStrip1.height;

const colorLabel1 = document.getElementById('color-label1');
const colorLabel2 = document.getElementById('color-label2');

let x = 0;
let y = 0;
let drag = false;

ctx1.rect(0, 0, colorBlockWidth, colorBlockHeight);
ctx3.rect(0, 0, colorBlockWidth, colorBlockHeight);

function fillGradient(n) {
  let context1;
  let context2;
  let rgbaColor;

  if (n === 1) {
    context1 = ctx1;
    context2 = ctx2;
    rgbaColor = AppState.firstColor;
  } else {
    context1 = ctx3;
    context2 = ctx4;
    rgbaColor = AppState.secondColor;
  }
  context1.fillStyle = rgbaColor;
  context1.fillRect(0, 0, colorBlockWidth, colorBlockHeight);

  const grdWhite = context2.createLinearGradient(0, 0, colorBlockWidth, 0);
  grdWhite.addColorStop(0, 'rgba(255,255,255,1)');
  grdWhite.addColorStop(1, 'rgba(255,255,255,0)');
  context1.fillStyle = grdWhite;
  context1.fillRect(0, 0, colorBlockWidth, colorBlockHeight);

  const grdBlack = context2.createLinearGradient(0, 0, 0, colorBlockHeight);
  grdBlack.addColorStop(0, 'rgba(0,0,0,0)');
  grdBlack.addColorStop(1, 'rgba(0,0,0,1)');
  context1.fillStyle = grdBlack;
  context1.fillRect(0, 0, colorBlockWidth, colorBlockHeight);
}

fillGradient(1);
fillGradient(2);


function fillStrip(n) {
  let context;
  if (n === 1) {
    context = ctx2;
  } else {
    context = ctx4;
  }
  context.rect(0, 0, colorStripWidth, colorStripHeight);

  const grd1 = context.createLinearGradient(0, 0, 0, colorBlockHeight);
  grd1.addColorStop(0, 'rgba(255, 0, 0, 1)');
  grd1.addColorStop(0.17, 'rgba(255, 255, 0, 1)');
  grd1.addColorStop(0.34, 'rgba(0, 255, 0, 1)');
  grd1.addColorStop(0.51, 'rgba(0, 255, 255, 1)');
  grd1.addColorStop(0.68, 'rgba(0, 0, 255, 1)');
  grd1.addColorStop(0.85, 'rgba(255, 0, 255, 1)');
  grd1.addColorStop(1, 'rgba(255, 0, 0, 1)');
  context.fillStyle = grd1;
  context.fill();
}

fillStrip(1);
fillStrip(2);

function click(e) {
  x = e.offsetX;
  y = e.offsetY;
  if (e.target.id === 'color-strip1') {
    const imageData = ctx2.getImageData(x, y, 1, 1).data;
    AppState.firstColor = `rgba(${imageData[0]},${imageData[1]},${imageData[2]},1)`;
    fillGradient(1);
  }
  if (e.target.id === 'color-strip2') {
    const imageData = ctx4.getImageData(x, y, 1, 1).data;
    AppState.secondColor = `rgba(${imageData[0]},${imageData[1]},${imageData[2]},1)`;
    fillGradient(2);
  }
}

function changeColor(e) {
  x = e.offsetX;
  y = e.offsetY;

  if (e.target.id === 'color-block1') {
    const imageData = ctx1.getImageData(x, y, 1, 1).data;
    AppState.firstColor = `rgba(${imageData[0]},${imageData[1]},${imageData[2]},1)`;
    colorLabel1.style.backgroundColor = AppState.firstColor;
  }

  if (e.target.id === 'color-block2') {
    const imageData = ctx3.getImageData(x, y, 1, 1).data;
    AppState.secondColor = `rgba(${imageData[0]},${imageData[1]},${imageData[2]},1)`;
    colorLabel2.style.backgroundColor = AppState.secondColor;
  }
}

function mousedown(e) {
  drag = true;
  changeColor(e);
}

function mousemove(e) {
  if (drag) {
    changeColor(e);
  }
}

function mouseup() {
  drag = false;
}

colorStrip1.addEventListener('click', click, false);
colorStrip2.addEventListener('click', click, false);
colorBlock1.addEventListener('mousedown', mousedown, false);
colorBlock2.addEventListener('mousedown', mousedown, false);
colorBlock1.addEventListener('mouseup', mouseup, false);
colorBlock2.addEventListener('mouseup', mouseup, false);
colorBlock1.addEventListener('mousemove', mousemove, false);
colorBlock2.addEventListener('mousemove', mousemove, false);

const colorPicker1 = document.getElementById('color-picker1');
const colorPicker2 = document.getElementById('color-picker2');
colorPicker1.style.top = `${colorLabel1.getBoundingClientRect().top}px`;
colorPicker2.style.top = `${colorLabel2.getBoundingClientRect().top}px`;
colorPicker1.style.left = `${colorLabel1.getBoundingClientRect().left + 65}px`;
colorPicker2.style.left = `${colorLabel2.getBoundingClientRect().left + 65}px`;

function showHideFirstPalette() {
  if (colorPicker1.style.display === 'none') {
    colorPicker1.style.display = 'block';
    colorPicker2.style.display = 'none';
  } else {
    colorPicker1.style.display = 'none';
  }
  fillGradient(1);
}

function showHideSecondPalette() {
  if (colorPicker2.style.display === 'none') {
    colorPicker2.style.display = 'block';
    colorPicker1.style.display = 'none';
  } else {
    colorPicker2.style.display = 'none';
  }
  fillGradient(2);
}

colorLabel1.addEventListener('click', showHideFirstPalette);
colorLabel2.addEventListener('click', showHideSecondPalette);

function wrapColor() {
  const buffer = AppState.firstColor;
  AppState.firstColor = AppState.secondColor;
  AppState.secondColor = buffer;
  colorLabel1.style.backgroundColor = AppState.firstColor;
  colorLabel2.style.backgroundColor = AppState.secondColor;
  fillGradient(1);
  fillGradient(2);
}

const colorWrapper = document.getElementById('color-wrapper');

colorWrapper.addEventListener('click', wrapColor);
